import React ,{useEffect,useState ,useContext}from 'react'
import { NavLink ,useNavigate} from 'react-router-dom'
import axios from 'axios';
import { motion } from "framer-motion"
import { AuthContext } from '../context/AuthContext';
import SectionChilds from '../components/Sectionchilds';
export default function Home() {

  const navigate =useNavigate
  const storedToken = localStorage.getItem('authToken');
  const { isLoggedIn, user, logOutUser } = useContext(AuthContext);
  console.log()
  const [childs, setChilds] = useState("");
  let isChilds =false
  let text =""
  if(childs.length !== 0){
    isChilds=true
    text =""
  }else{
     isChilds=false
     text ="No child found"
  }
  useEffect(() => {    
    const getData = async () => {
      try {
        const response = await axios.get(`${process.env.REACT_APP_API_URL}/child/mine`,{ headers: { Authorization: `Bearer ${storedToken}` }});
        setChilds(response.data.data)        
      } catch (error) {
        console.error(error);
      }
    }
    getData();
  }, []);
  return (
    <motion.div className='logo' 
    animate={{
      scale:[-0.9,8,4],
      rotate:[1,30,-30,0]
    }}   
    transition={{duration:2}} 
    >    
    <div>    
      <h1>Welcome <br/>HomeWork<br/>for<br/>kids</h1> 
    </div>    
    </motion.div>
  )
}     
          {/* {isChilds && (
                <SectionChilds/>                
          )}
          {!isChilds && (
                 <NavLink to="./AddChild"><button>Add new child</button></NavLink>
          )} */}